import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { UseFormSetValue } from 'react-hook-form/dist/types/form';
import { Dispatcher } from '../../../../types/store';
import { fetchCar } from '../../../../store/Cars/thunks';
import { OrderStatus, OrderStatusForm, OrderStatusState } from '../../../../types/OrderStatus';

export const useOrderStatusById = (orderStatusId: string, setValue: UseFormSetValue<OrderStatusForm>): [OrderStatus, boolean] => {
  const dispatch = useDispatch<Dispatcher>();
  const { orderStatusById, orderStatusByIdLoading, orderStatusByIdError } = useSelector<{
    orderStatus: OrderStatusState;
  }, OrderStatusState>((state) => state.orderStatus);

  const setValuesToForm = async (orderStatus): Promise<void> => {
    setValue('name', orderStatus?.name, { shouldDirty: true });
  };

  useEffect(() => {
    if (orderStatusById) {
      dispatch(fetchCar(orderStatusById));
    }
  }, [orderStatusById]);

  useEffect(() => {
    if (orderStatusById && orderStatusId) {
      setValuesToForm(orderStatusById);
    }
  }, [orderStatusById]);

  return [orderStatusById, orderStatusByIdLoading];
};
